<script setup>
  import {
    Odometer,
    Operation,
    Tickets,
    Setting,
    Bell
  } from '@element-plus/icons-vue'
  import { ref } from 'vue'
  import { useRoute, useRouter } from 'vue-router'
  import {ElMessage} from 'element-plus';

  const route = useRoute()

  const menus = ref([
    {
      title: '平台首页',
      icon: Odometer,
      path: '/home'
    },
    {
      title: '集群列表',
      icon: Tickets,
      path: '/clusterlist'
    },
    {
      title: '任务中心',
      icon: Operation,
      path: '/taskcenter'
    }
  ])

  const defaultActive = ref('/home')

  defaultActive.value = route.path

  const pageTitle = ref('')

  function menuSelect(index) {
    let menu = menus.value.find(item => {
      return item.path == index
    }) || {}

    pageTitle.value = menu.title || ''
  }

  menuSelect(defaultActive.value)
</script>

